import React, { Component } from 'react'
import { TextField, Button } from '@material-ui/core';
import { createHash } from 'crypto';

export default class PasswordProtection extends Component {
    constructor(props) {
        super(props);
        this.state = {
            passwordValue: '',
            error: false
        };
    }

    handleChange = e => {
        this.setState({ 
            passwordValue: e.target.value,
            error: false
        });
    }

    handleClick = () => {
        const targetHash = '4a7a11ce9ef624ae2f3ae9be76af8e6b03f66ae9eb6642c54079e2059fd30b22c63ff919ea19dd72f5e56d8b269002794c48f4a2c50ba9a782789f81d3f2e62e';
        const hashedPassword = createHash('sha512').update(this.state.passwordValue).digest('hex');
        if (hashedPassword === targetHash) {
            this.props.onSuccess();
        }
        else {
            this.setState({ error: true });
        }
    }

    render() {
        return (
            <div>
                <div>
                    <TextField
                        label="Password"
                        variant="outlined"
                        type="password"
                        error={this.state.error}
                        value={this.state.passwordValue}
                        onChange={this.handleChange}
                    />
                </div>
                <div style={{ marginTop: '15px' }}>
                    <Button
                        variant="contained"
                        color="primary"
                        type="submit"
                        onClick={this.handleClick}
                    >
                        Entrar
                    </Button>
                </div>
            </div >
        );
    }
}
