import React, { Component } from 'react'
import Select from '@material-ui/core/Select';
import { MenuItem } from '@material-ui/core';
import './App.css';

const caracteristicas = [
    '1 color', '2 colores', '2+ colores', 'Agujeritos', 'Azul', 'Beige', 'Blanca', 'Clara', 'Corazones', 'Dibujos',
    'Encaje', 'Estrellas', 'Formas geométricas', 'Gris', 'Lisa', 'Lunares', 'Moñitos', 'Negra', 'Oscura', 'Palmeras', 'Piel',
    'Plateada', 'Puntitos', 'Rayas', 'Rayitas', 'Roja', 'Rosa', 'Sin costuras', 'Super lisa', 'Transparencia', 'Amarilla',
    'Verde', 'Puntilla', 'Ropa'
];

export default class Pistas extends Component {
    constructor(props) {
        super(props)

        this.state = {
            tipo: 'positivo',
            valor: ''
        }
    }

    render() {
        return (
            <div className="pista">
                <Select
                    onChange={(e) => this.onTipoChange(e)}
                    value={this.state.tipo}
                    className="pista tipo"
                >
                    <MenuItem key={this.props.id + 'positivo'} value='positivo'>Positivo</MenuItem>
                    <MenuItem key={this.props.id + 'negativo'} value='negativo'>Negativo</MenuItem>
                </Select>
                <Select
                    onChange={(e) => this.onCaracterisitcaChange(e)}
                    value={this.props.value}
                    className="pista caracteristica"
                >
                    {caracteristicas.map(item => (<MenuItem key={`${this.props.id}${item}`} value={item}>{item}</MenuItem>))}
                </Select>
            </div>
        );
    }

    onTipoChange(e) {
        this.setState({ tipo: e.target.value }, () => this.props.onChange(this.props.id, this.state.valor, this.state.tipo));
    }

    onCaracterisitcaChange(e) {
        this.setState({ valor: e.target.value }, () => this.props.onChange(this.props.id, this.state.valor, this.state.tipo));
    }
}
