import React, { Component } from 'react';
import './App.css';
import Pistas from './Pistas'
import Finder from './Finder'
import { Button } from '@material-ui/core';
import { Add, Remove } from '@material-ui/icons'
import PasswordProtection from './PasswordProtection'

export default class App extends Component {
    constructor(props) {
        super(props)

        this.state = {
            pistas: [{ id: 0, value: '' }],
            passwordProtected: true
        };
    }

    render() {
        return (
            <div className="App">
                {this.state.passwordProtected && (
                    <PasswordProtection onSuccess={() => this.setState({ passwordProtected: false })} />
                )}
                {!this.state.passwordProtected && (
                    <>
                        <div className="pistas-container">
                            {this.state.pistas.map(x => (
                                <Pistas key={x.id} onChange={this.handleChange} id={x.id} value={x.value} />
                            ))}
                        </div>

                        <div className="buttons">
                            <Button
                                onClick={this.remove}
                                variant='outlined'
                                className="button"
                            >
                                <Remove />
                            </Button>
                            <Button
                                onClick={this.add}
                                color='primary'
                                variant='outlined'
                                className="button"
                            >
                                <Add />
                            </Button>
                        </div>
                        <div>
                            <Finder pistas={this.state.pistas} />
                        </div>
                    </>
                )}
            </div>
        );
    }

    add = () => {
        let pistas = this.state.pistas.slice();
        const newId = pistas.map(x => x.id).reduce((max, i) => max > i ? max : i, 0) + 1;
        pistas.push({ id: newId, value: '' });
        this.setState({ pistas: pistas });
    }

    remove = () => {
        this.setState({ pistas: this.state.pistas.slice(0, this.state.pistas.length - 1) });
    }

    handleChange = (id, value, tipo) => {
        const pistas = this.state.pistas.slice();
        const pista = pistas.find(x => x.id === id);
        pista.value = value;
        pista.tipo = tipo;
        this.setState({ pistas: pistas });
    }
}
