import React, { Component } from 'react'
import bombachasData from './bombachas'
import './App.css'

export default class Finder extends Component {
    render() {
        return (
            <div>
                {this.find()}
            </div>
        )
    }

    find = () => {
        if (this.props.pistas.length === 0)
            return <></>;

        const pistasPositivas = this.props.pistas.slice().filter(x => x.tipo === 'positivo').map(x => x.value).filter(x => x !== '');
        const pistasNegativas = this.props.pistas.slice().filter(x => x.tipo === 'negativo').map(x => x.value).filter(x => x !== '');
        const bombachas = bombachasData.slice();

        let filtradas = [];
        bombachas.forEach(bombacha => {
            if (pistasPositivas.every(pista => bombacha.caracteristicas.includes(pista)) &&
                pistasNegativas.every(pista => !bombacha.caracteristicas.includes(pista)))
                filtradas.push(bombacha);
        });

        let caracteristicasRepetidas = [];
        filtradas.forEach(bombacha => bombacha.caracteristicas.forEach(c => {
            let caracteristica = caracteristicasRepetidas.find(x => x.name === c);
            if (caracteristica) {
                caracteristica.value++;
            }
            else {
                caracteristicasRepetidas.push({ name: c, value: 1 });
            }
        }));
        caracteristicasRepetidas = caracteristicasRepetidas.sort((x, y) => y.value - x.value);

        return (
            <>
                <div className="bombachas">
                    Hay {filtradas.length} bombachas&nbsp;
                    {filtradas.length > 0 && ((1 / filtradas.length).toFixed(2) + "%")}
                    <ul>
                        {filtradas.map(x => <li key={x.nombre}>{x.nombre}</li>)}
                    </ul>
                </div>
                <div className="resumen">
                    Por características:
                    <ul>
                        {caracteristicasRepetidas.map(x => (
                            <li key={x.value + x.name}>{x.value} {x.name}</li>
                        ))}
                    </ul>
                </div>
            </>
        );
    }
}
